import {ajax,setTime,showTips,showLoading,hideLoading} from '@/ajax.js'
export default {
	data() {
		return {
			auheight:'',
			id:'',
			info:'',  		//初始化数据
			toUserName:'',  //留言的对象
			toUserId:'',	//留言的对象ID
			comment:''      //回复帖子内容
		}
	},
	onLoad(e) {
		this.id = e.id;
		uni.setNavigationBarTitle({
			title: '详情'
		});
		this.infoFun()
	},
	onShow() {
		let _this = this
		uni.getSystemInfo({
			success: function (res) {
				_this.auheight = res.windowHeight
			}
		});
	},
	methods: {
		focusFun(e){
			this.auheight= this.auheight - e.detail.height;
		},
		blurFun(e){
			let _this = this
			uni.getSystemInfo({
				success: function (res) {
					_this.auheight = res.windowHeight
				}
			});
		},
		infoFun(){
			let _this = this;
			let login = uni.getStorageSync('login')
			if(login!=''&&login!=null&&login!=undefined){
				showLoading()
				uni.request({
					url: ajax.detail+ this.id, //仅为示例，并非真实接口地址。
					header:{
						Authorization:'bearer' +' '+ login
					},
					success: (res) => {
						hideLoading()
						if(res.data.code==200){
							_this.info = res.data.data;
							_this.info.newTime = setTime(res.data.data.date)
							if(_this.info.comments.length>0){
								_this.info.comments.map(es=>{
									es.newTime = setTime(es.date)
								})
							}
						}
					}
				});
			}
		},
		toUser(e){
			console.log(e)
			this.toUserName = e.author
			this.toUserId = e._id
		},
		submit(){
			let login = uni.getStorageSync('login')
			if(login!=''&&login!=null&&login!=undefined){
				if(this.toUserName!=''){ //给别人留言
					uni.request({
						url: ajax.replyreply,
						header:{
							Authorization:'bearer' +' '+ login
						},
						method:'POST',
						data:{
							blog_id: this.info._id,
							reply_id: this.toUserId,
							comment: this.comment
						},
						success: (res) => {
							if(res.data.code==200){
								showTips('留言成功')
								this.toUserName='';
								this.comment='';
								this.toUserId='';
								this.infoFun()
							}
						}
					});
				}else{  //给文章留言
					uni.request({
						url: ajax.reply,
						header:{
							Authorization:'bearer' +' '+ login
						},
						method:'POST',
						data:{
							id: this.info._id,
							comment: this.comment
						},
						success: (res) => {
							if(res.data.code==200){
								showTips('留言成功')
								this.toUserName='';
								this.comment='';
								this.toUserId='';
								this.infoFun()
							}
						}
					});
				}
			}
		}
	}
}
